#promotio_latest {
    @extend #hp_hotel_11;
    .col-lg-4 {
        padding: 0 0 0 20px;
        @media (max-width:$screen-sm-max) {
          padding: 20px 15px;
        }
        .details {
            .inner {
                h4 {
                    font-size: 24px;
                    line-height: 1.35;
                    color: #111;
                    letter-spacing: 4px;
                    @media (max-width:$screen-sm-max) {
                      font-size: 20px;
                    }
                }
                p {
                    font-size: 14px;
                    line-height: 22px;
                    color: #111;
                }
                a {
                    color: #79451b;
                    display: inline-block;
                    margin-top: 15px;
                }
            }
        }
    }
}
