#newsDetail_02 {
    margin-top: 80px;
    @media(max-width: $screen-sm-max) {
        margin-top: 47px;
        padding: 0 12px;
    }
    .container {
        .row {
            .newsDetail_left {
                h2 {
                    font-size: 24px;
                    color: #343434;
                    margin-bottom: 28px;
                    @media(max-width: $screen-sm-max) {
                        font-size: 20px;
                        line-height: 27px;
                        color: #111111;
                        margin-bottom: 13px;
                    }
                }
                p {
                    margin: 0px;
                    padding: 0px;
                    font-size: 14px;
                    line-height: 22px;
                    color: #343434;
                    margin-bottom: 28px;
                    @media(max-width: $screen-sm-max) {
                        font-size: 14px;
                        line-height: 22px;
                        color: #343434;
                        margin-bottom: 30px;
                    }
                }
                img {
                    width: 100%;
                    height: auto;
                    margin-bottom: 28px;
                    @media(max-width: $screen-sm-max) {
                        margin-bottom: 25px;
                    }
                }
            }
            .newsDetail_right {
                @media(max-width: $screen-sm-max) {
                    margin-top: 17px;
                }
                h2 {
                    font-size: 24px;
                    color: #111111;
                    margin-bottom: 26px;
                    margin-top: 0px;
                    @media(max-width: $screen-sm-max) {
                        font-size: 20px;
                        margin-bottom: 14px;
                    }
                }
                .advertisement {
                    margin-bottom: 36px;
                    @media(max-width: $screen-sm-max) {
                        margin-bottom: 50px;
                    }
                    img {
                        width: 100%;
                        height: auto;
                    }
                    h4 {
                        font-size: 24px;
                        color: #111111;
                        margin: 22px 0 12px;
                        line-height: 30px;
                        @media(max-width: $screen-sm-max) {
                            font-size: 20px;
                            line-height: 27px;
                            margin: 26px 0 15px;
                        }
                    }
                    >span {
                        display: none;
                        @media(max-width: $screen-sm-max) {
                            display: block;
                            font-size: 14px;
                            line-height: 22px;
                            color: #343434;
                            margin-bottom: 15px;
                        }
                    }
                    a {
                        font-size: 14px;
                        color: #644c2b;
                    }
                }
            }
        }
    }
}